// @import "../../../../assets/scss/common.scss";
@import "../../../assets/scss/common.scss";

.basicInfo {
  padding: 5px;

  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }

  hr {
    border-top: 2px solid #e8e9e9;
  }

  .btn {
    column-gap: 17px;
  }

  .rowWidth {
    width: 378px;
  }

  .AddressWidth {
    width: 540px;
  }

  .btnWidth {
    width: 160px;
  }

  .propertyLabel {
    color: #090909;
  }

  .basicForm {
    background: #fafafa;
    border-radius: 16px;

    .iconbtn {
      padding: 0;
      margin-right: 8px;
      width: 18px;
      height: 18px;
      color: #935ed6;

      .circleAdd {
        width: 18px;
        height: 18px;
        color: #935ed6;
      }
    }
  }

  .basicForm2 {
    // background: #FAFAFA;
    border-radius: 16px;
  }

  .gap {
    gap: 10px;
  }

  .sequence {
    color: var(--primary-03-main, #1e2528);

    /* Body Text/B3-Regular */
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    /* 142.857% */
  }

  .checkboxWrap {
    display: flex;
    padding-right: 19px;
    align-items: center;

    .checkbox {
      margin-top: 0;
      margin-right: 8px;
    }

    .selectText {
      @extend .B3-Medium;
      color: #1e2528;
    }
  }

  .hr {
    height: 40px;
    width: 1px;
    background-color: #d2d3d4;
  }

  .addServiceWrap {
    padding-left: 19px;
    text-decoration: none;
    cursor: pointer;

    .iconbtn {
      padding: 0;
      margin-right: 8px;
      width: 18px;
      height: 18px;
      color: #935ed6;

      .circleAdd {
        width: 18px;
        height: 18px;
        color: #935ed6;
      }
    }

    .addServiceText {
      @extend .B3-Medium;
      color: #935ed6;
    }
  }

  .disabled {
    opacity: 0.5;
    user-select: none;
    cursor: default;
  }

  .selectwrap {
    max-width: 220px;
    margin-left: auto;
    display: flex;
  }

  .position-relative {
    position: relative;
    display: inline-block;
  }

  .tooltip {
    display: none;
    position: absolute;
    top: -30px;
    /* Adjust the distance as needed */
    left: 50%;
    transform: translateX(-50%);
    background-color: #333;
    color: #fff;
    padding: 5px;
    border-radius: 5px;
    z-index: 1;
    /* Place the tooltip on top */
  }

  .tooltip.show {
    display: block;
  }

  .actions {
    display: inline-block;
    position: relative;
  }

  .info {
    border-radius: 4px;
    background: #fff;

    /* Dropdown Shadow */
    box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
    padding: 4px 8px;
    @extend .B3-Medium;
    color: var(--primary-03-main, #1e2528);

    /* Caption/C1-Medium, no underline */
    font-family: Barlow;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    /* 150% */
  }

  .filters {
    border-radius: 16px;
    border: 1px solid var(--primary-09, #f6f6f6);

    /* Dropdown Shadow */
    box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
  }

  /* Style for the overlay */
  .overlay {
    // display: flex;
    position: absolute;
    top: 9em;
    left: 0;
    padding-right: 102px;
    justify-content: end;
    width: 100%;
    height: 100%;
    /* background: rgba(0, 0, 0, 0.5); */
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: end;
  }

  /* Style for the filters frame */
  .filtersFrame {
    width: 408px;
    height: 268px;
    background: var(--primary-09, #f6f6f6);
    /* Background color of the frame */

    z-index: 3;
    /* Higher z-index to display above the overlay */
    /* Add other styling for your frame as needed */
    border-radius: 16px;
    border: 1px solid var(--primary-09, #f6f6f6);

    /* Dropdown Shadow */
    box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
  }

  /* Dropdown*/

  .dropdownCTA {
    align-items: flex-start;
    display: flex;
    gap: 16px;
    position: relative;
    //   width: 170px;
  }

  .today {
    color: #4b5153;
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;

    position: relative;
    white-space: nowrap;
    width: fit-content;
    padding: 15px 7px 0px 12px;
  }

  .statusDropdown {
    position: absolute;
    top: 47px;
    left: 0;
    width: 60%;
    max-height: 200px;
    overflow-y: auto;
    background: #fafbfb;
    border: 1px solid #d2d3d4;
    z-index: 3;
    display: flex;
    flex-direction: column;
    // align-items: center;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
  }

  .statusOption {
    padding: 10px 15px;
    cursor: pointer;
    transition: background-color 0.2s;
    color: #4b5153;
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    position: relative;
    white-space: nowrap;
    width: 170px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #d2d3d4;
  }

  .statusOption:hover {
    background-color: #f2f3f4;
  }

  .textwrapper {
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    color: #a5a8a9;
  }

  select.dropdownSelect {
    outline: none;
    border: none;

    padding: 5px;
  }

  .div5 {
    align-items: center;
    display: inline-flex;
    border-radius: 10px;
    border: 1px solid var(--primary-07, #d2d3d4);
    background: #fff;
    gap: 16em;
  }
}